import { Injectable, Inject } from '@angular/core';
import { from, Observable } from 'rxjs';
import { ApiService } from '../api/api.service';
declare const gapi: any;

@Injectable({
  providedIn: 'root'
})
export class LogStatusService {

  private _LOGINSTATUS = '';

  get logStatus(): string {
    return this._LOGINSTATUS;
  }

  set logStatus(status: string) {
    this._LOGINSTATUS = status;
  }

  TOKEN = 'accessToken';

  auth2;

  constructor() {
    const token = localStorage.getItem(this.TOKEN);

  }

  // 判斷localStorage裡面是否存在token
  loadLocalStorage() {
    const token = localStorage.getItem(this.TOKEN);
    if (token) {
      return false;
    }
    return true;
  }

  googleInit() {
    gapi.load('auth2', () => {
      this.auth2 = gapi.auth2.init({
        client_id: '548588976225-9vn6rb0sv6u2p1d7fk19rr57r9mh4lmu.apps.googleusercontent.com',
        cookie_policy: 'single_host_origin',
        scope: 'profile email'
      });
    });
  }

  onSignIn(element?) {
    const obs = from(this.auth2.signIn());
    return new Observable(subscriber => {
      obs.subscribe((googleUser: any) => {
        const token = googleUser.getAuthResponse().id_token;
        localStorage.setItem(this.TOKEN, token);
        console.log('Token || ' + googleUser.getAuthResponse().id_token);
        const profile = googleUser.getBasicProfile();
        console.log('ID: ' + profile.getId());
        console.log('Name: ' + profile.getName());
        console.log('Image URL: ' + profile.getImageUrl());
        console.log('Email: ' + profile.getEmail());
        subscriber.next();
        subscriber.complete();
      });
    });


    //另一種寫法
    // this.auth2.attachClickHandler(element /*傳進來的HTML元素*/ */, {},
    //   (googleUser) => {
    //     const profile = googleUser.getBasicProfile();
    //     console.log('Token || ' + googleUser.getAuthResponse().id_token);
    //     console.log('ID: ' + profile.getId());
    //     console.log('Name: ' + profile.getName());
    //     console.log('Image URL: ' + profile.getImageUrl());
    //     console.log('Email: ' + profile.getEmail());
    //     // YOUR CODE HERE
    //     this.logStatus = this.login;
    //     console.log('this.logStatus',this.logStatus)
    //     console.log('this.LOGIN',this._LOGINSTATUS)
    //   }, (error) => {
    //     alert(JSON.stringify(error, undefined, 2));
    //   });
    // });
  }

}
